@import "../../sass/colors";
@import "../../sass/common";

#overlay_loader_cnt {
    @include displayFlexCenter;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 100vh;
    z-index: 20;
    flex-direction: column;
    background: $overlay;
  }
#loader_message{
  margin-top: 1rem;
  color: $black;
  font-size: 1rem
}
  #loader_cnt {
    @include displayFlexCenter;
    margin: 2rem 0;
  }
  
  .loader_shoe {
    position: relative;
    width: 10rem;
    height: 12rem;
    margin-top: -10rem;
    border: .3rem solid $white;
    background-color: $black;
    clip-path: polygon(
      66% 80%,
      45% 68%,
      37% 75%,
      23% 77%,
      15% 70%,
      9% 82%,
      10% 97%,
      11% 100%,
      48% 100%,
      78% 100%,
      89% 99%,
      91% 94%,
      87% 86%,
      78% 83%
    );
    animation: animateshoe 1s infinite;
    transition: all 0.3s;
  }
  #shoe_1 {
    // animation-delay: .2s;
  }
  #shoe_2 {
    animation-delay: .5s;
  }
  
  @keyframes animateshoe {
    0% {
      transform: translateY(0px);
    }
  
    10% {
      transform: translateY(-10px);
    }
    20% {
      transform: translateY(-20px);
    }
    25% {
      transform: translatex(10px);
    }
    45% {
      transform: rotate(15deg);
    }
    50% {
      transform: translatex(0px);
    }
  
    100% {
      transform: rotate(0deg);
    }
  }